import React from "react";
import "./App.css";
import { Route } from "react-router-dom";
import {
  Header,
  Navbar,
  Profile,
  DialogsContainer,
  News,
  Music,
  Settings,
} from "./Components";

const App = (props) => {
  return (
    <div className="app-wrapper">
      <Header />
      <Navbar />
      <div className="app-wrapper-content">
        
        <Route  path="/profile" 
                render={() => <Profile store={props.store} />} />
        <Route  path="/dialogs" 
                render={() => <DialogsContainer store={props.store} />} />
        <Route  path="/news" 
                render={() => <News />} />
        <Route  path="/music" 
                render={() => <Music />} />
        <Route  path="/settings" 
                render={() => <Settings />} />
      </div>
    </div>
  );
};

export default App;
